import { Link } from 'react-router-dom'


function Navbar() {
    return (
        <nav className="border-gray-200 px-2 sm:px-4 py-2.5  bg-gray-800">
            <div className="container flex flex-wrap justify-between items-center mx-auto">
                <div className="flex items-center">

                    <img src="https://upload.wikimedia.org/wikipedia/commons/4/4a/Usac_logo.png" className="mr-3 h-6 sm:h-9" alt="Logo" />
                    <Link to="/" >
                        <span className="self-center text-xl font-semibold whitespace-nowrap text-white">Biblioteca USAC</span>
                    </Link>
                </div>
                <button data-collapse-toggle="mobile-menu" type="button" className="inline-flex items-center p-2 ml-3 text-sm text-gray-500 rounded-lg md:hidden hover:bg-gray-100 focus:outline-none focus:ring-2 focus:ring-gray-200 text-gray-400 hover:bg-gray-700 focus:ring-gray-600" aria-controls="mobile-menu" aria-expanded="false">
                    <span className="sr-only">Open main menu</span>
                    <svg className="w-6 h-6" fill="currentColor" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg"><path fillRule="evenodd" d="M3 5a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1zM3 10a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1zM3 15a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1z" clipRule="evenodd"></path></svg>
                    <svg className="hidden w-6 h-6" fill="currentColor" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg"><path fillRule="evenodd" d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z" clipRule="evenodd"></path></svg>
                </button>
                <div className="hidden w-full md:block md:w-auto" id="mobile-menu">
                    <ul className="flex flex-col mt-4 md:flex-row md:space-x-8 md:mt-0 md:text-sm md:font-medium">

                        <li>
                            <Link to="/user" className="block py-2 pr-4 pl-3 text-white  md:hover:bg-transparent md:border-0 md:hover:text-blue-700 md:p-0 text-gray-400 md:hover:text-white hover:bg-gray-700 hover:text-white md:hover:bg-transparent">
                                Usuarios
                            </Link>
                        </li>

                        <li>
                            <Link to="/book" className="block py-2 pr-4 pl-3 text-white  md:hover:bg-transparent md:border-0 md:hover:text-blue-700 md:p-0 text-gray-400 md:hover:text-white hover:bg-gray-700 hover:text-white md:hover:bg-transparent">
                                Libros
                            </Link>
                        </li>

                        <li>
                            <Link to="/borrowscreen" className="block py-2 pr-4 pl-3 text-white  md:hover:bg-transparent md:border-0 md:hover:text-blue-700 md:p-0 text-gray-400 md:hover:text-white hover:bg-gray-700 hover:text-white md:hover:bg-transparent">
                                Prestamo
                            </Link>
                        </li>

                        <li>
                            <Link to="/reportscreen" className="block py-2 pr-4 pl-3 text-white  md:hover:bg-transparent md:border-0 md:hover:text-blue-700 md:p-0 text-gray-400 md:hover:text-white hover:bg-gray-700 hover:text-white md:hover:bg-transparent">
                                Reporte
                            </Link>
                        </li>
                    </ul>
                </div>
            </div>
        </nav>

    )
}

export default Navbar